import Vue from 'vue'
import Router from 'vue-router'
const Index = () => import('@/pages/Index')
const findoutPiggy = () => import('@/pages/FindoutPiggy')
const memoryCard = () => import('@/pages/MemoryCard')
const scratchFun = () => import('@/pages/ScratchFun')

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/findoutpiggy',
      name: 'FindoutPiggy',
      component: findoutPiggy
    },
    {
      path: '/memorycard',
      name: 'MemoryCard',
      component: memoryCard
    },
    {
      path: '/scratchfun',
      name: 'ScratchFun',
      component: scratchFun
    }
  ]
})



